body{
    padding: 1rem;
}

.container{
    max-width: 600px;
    margin: auto;
}

.done{
    text-decoration: line-through;
}

ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

.item{
    display: flex;
    justify-content: space-between;
    padding: 15px;
    margin-top: 15px;
    font-size: 20px;
}

.card{
    box-shadow: 0px 2px 7px 1px rgba(0, 0, 0, 0.2);
}

.modal{
    background-color: rgba(0, 0, 0, 0.2);
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal .card{
    background-color: white;
    padding: 15px;
}

.hide{
    display: none;
}

.add-button{
    height: 50px;
    width: 50px;
    background-color: rgb(127, 214, 255);
    color: white;
    border: none;
    border-radius: 25px;
}

header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}


button{
    cursor: pointer;
}